html{
  scroll-behavior: smooth;
}

body{
    background-color: #242424;
    margin: 0;
}

h1, h2, h3, h4, h5, h6, li{
    font-family: 'Work Sans', sans-serif;
    font-weight: 100;
}

p, a{
  font-family: 'Work Sans', sans-serif;
  font-weight: 100;
}

strong{
  font-family: 'Work Sans', sans-serif;
  font-weight: 600;
}

button{
  background: none;
  stroke: none;
  border: none;
  color: white;
  margin: 0px 25px;
  font-family: 'Work Sans', sans-serif;
  font-weight: 100;
  font-size: 16px;
  &.current{
    font-weight: 600;
  }
}

button:hover{
  cursor: pointer;
}

a{
  text-decoration: none;
  color: inherit;
}

.featured-item-description a, .work-grid a{
    text-decoration: none;
    text-decoration: underline;
}

a:visited{
    color: inherit;
    text-decoration: none;
}

.homepage-module{
    position: relative;
    display: block;
    height: 100vh; 
    width: 100vw;
    height: 95vh;
    margin-top: 100px;
}

.absolute-center{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.module-header-left{
  margin: 0px;
  margin-left: 60px;
  text-align: left;
  color: white;
  h2{
    font-size: 35px;
    // padding: 25px 0px;
  }
  p{
    width: 620px;
    margin-top: 50px;
    margin-bottom: 0px;
  }
}
.module-header-right{
  margin: 0px;
  margin-right: 60px;
  text-align: right;
  color: white;
  h2{
    font-size: 35px;
  }
  p{
    margin-top: 50px;
    margin-bottom: 0px;
    display: inline-block;
    width: 620px;
  }
}


@mixin global-transition {
    transition: 0.125s ease-in-out, background-color 0.2s ease-out, color 0.1s ease-out, border 0.1s ease-out;
}
  
  .timeline {
    // padding: 50px;
    position: absolute;
    ul {
      padding: 0;
    }
  
    .default-line {
      content: '';
      position: absolute;
      left: 40px;
      width: 4px;
      background: #494949;
      height: 1525px;
    }
  
    .draw-line {
      width: 4px;
      height: 0;
      position: absolute;
      left: 40px;
      background: #bac6dc;
    }
  
    ul li {
      list-style-type: none;
      position: relative;
      width: 2px;
    //   margin: 0 auto;
      height: 515px;
      background: transparent;
  
      &.in-view {
        @include global-transition;
  
        &::before {
          content: '';
          position: absolute;
          left: 40px;
          top: 0;
          transform: translateX(-45%);
          width: 42px;
          height: 42px;
        //   border-radius: 50%;
          background-color: #bac6dc;
          background-size: 20px 20px;
          background-repeat: no-repeat;
          background-position: center;
          @include global-transition;
        }
        &::before:nth-child(1){
            content: '1/3';
        }
        &::before:nth-child(2){
            content: '2/3';
        }
        &::before:nth-child(3){
            content: '3/3';
        }
      }
  
      &::before {
        content: '';
        position: absolute;
        left: 40px;
        top: 0;
        transform: translateX(-45%);
        width: 12px;
        height: 12px;
        // border-radius: 50%;
        background: inherit;
        background: #494949;
        transition: all .4s ease-in-out;
      }
    }
    li div p{
        display: none;
        position: absolute;
        font-weight: 600;
        left: 30px;
        top: -5px;
    }
    .in-view div p{
      display: block;
    }
  }

  @media screen and (max-width: 768px) {
    .module-header-left{
      margin-left: 15px;
      h2{
        font-size: 30px;
      }
      p{
        width: 95vw;
        margin-top: 25px;
      }
    }
    .module-header-right{
      margin-right: 15px;
      h2{
        font-size: 30px;
      }
      p{
        width: 95vw;
        margin-top: 25px;
      }
    }
    
  }